@font-face {
  font-family: 'Spoqa Han Sans';
  src: url("/static/media/Spoqa Han Sans Bold.f6e14af6.ttf");
}

.paper {
  border: 2px solid;
  width: 90%;
  margin: auto;
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  min-height: 200px;
  grid-template-rows: 50px auto;
  font-family: 'Spoqa Han Sans';
}

.title {
  grid-column-start: 1;
  grid-column-end: 5;
  display: flex;
  justify-content: space-around;
  align-items: center;
  /* border-bottom: 2px solid; */
  font-weight: bold;
  letter-spacing: 0.7px;
}

.paper-bt {
  border: none;
  cursor: pointer;
}

.paper-bt:focus {
  outline: none;
}

@media screen and (max-width:1199px) {
  .paper {
    grid-template-columns: 33.333% 33.333% 33.333%;
    width: 90%;
    min-width: 690px;
  }

  .title {
    grid-column-end: 4;
  }
}

@media screen and (max-width:767px) {
  .paper {
    grid-template-columns: 100%;
    width: 90%;
    min-width: 230px;
  }

  .title {
    grid-column-end: 2;
  }
}

.loginBox {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    min-width: 300px;
    text-align: center;
}

h1 {
    color: mediumslateblue;
}

input {
    margin-bottom: 10px;
    padding: 11px 17px;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    outline: none;
    line-height: normal;
    border: 1px solid transparent;
    border-bottom-color: rgba(0, 0, 0, 0.3);
    background-color: transparent;
    width: 60%;
}

input:active, input:focus {
    border-bottom-color: mediumslateblue;
}

.loginBox > button {
    margin-top: 20px;
    height: 30px;
    width: 70px;
    background-color: mediumslateblue;
    border: 2px solid mediumslateblue;
    color: white;
    cursor: pointer;
}
  
.loginBox > button:hover {
    background-color: transparent;
    color: black;
}
